img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.wrapper {
  max-width: 1400px;
  padding: 0 5px;
  margin: 0 auto;

  @media (max-width: $mobile-width) {
    padding: 0;
  }
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}
